<template>
  <div class="step_container">
    <div class="itemsArea">
      <!--    -->

      <v-row class="mb-3">
        <v-col cols="12">
          <v-btn-toggle
            v-model="uploadMode"
            mandatory
            class="btn-toggle-full custom-toggle"
          >
            <v-btn value="manual"> </v-btn>
            <v-btn value="file"> </v-btn>
          </v-btn-toggle>
        </v-col>
      </v-row>

      <!--  Native Drop  -->
      <div v-show="uploadMode === 'file'" class="upload-wrapper">
        <div
          ref="nativeDropZone"
          class="native-drop-zone"
          @click="triggerNativeFile"
          @dragover.prevent="dragging = true"
          @dragleave.prevent="dragging = false"
          @drop.prevent="onNativeDrop"
          :class="{ active: dragging }"
        >
          <!--   /   -->
          <template v-if="fileName">
            <div class="file-display">
              <v-icon color="primary" size="20">mdi-file</v-icon>
              <span class="ml-2"> {{ fileName }} ({{ fileSize }}) </span>
              <v-btn
                icon
                variant="text"
                color="error"
                size="x-small"
                class="ml-1"
                @click.stop="clearFile"
              >
                <v-icon size="16">mdi-close</v-icon>
              </v-btn>
            </div>
          </template>

          <template v-else>
            <p>
                  <strong></strong><br />
              
            </p>
          </template>

          <!--  input -->
          <input
            ref="nativeFileInput"
            type="file"
            accept=".csv,.xlsx,.txt, .json"
            style="display: none"
            @change="onNativeFileChange"
          />
        </div>
      </div>

      <div :class="['gray-panel', { disabled: uploadMode === 'file' }]">
        <v-row>
          <v-col>
            <v-card class="demandArea">
              <v-card-title></v-card-title>
              <v-card-text>
                <v-text-field
                  label=" "
                  variant="outlined"
                  v-model="demandArea"
                  placeholder=" "
                  readonly
                  prepend-inner-icon="mdi-magnify"
                  @click="openPostcode"
                  color="primary"
                  class="mb-3"
                  hide-details
                />
                <v-btn class="round Item" @click="addDemandArea">  </v-btn>
                <!-- 
              <v-text-field
                label=""
                type="number"
                variant="outlined"
                v-model="electricCapacity"
                prepend-inner-icon="mdi-lightning-bolt"
                color="primary"
                class="mb-3"
                hide-details
              >
                <template #append-inner>
                  <span class="text-subtitle-2 pr-2">MW</span>
                </template>
              </v-text-field>

              <v-text-field
                label="  "
                type="number"
                variant="outlined"
                v-model="hoursPerDay"
                prepend-inner-icon="mdi-hours-24"
                color="primary"
                @update:model-value = 'validateHoursPerDay'
              >
                <template #append-inner>
                  <span class="text-subtitle-2 pr-2">hr/day</span>
                </template>
              </v-text-field> -->
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>

        <v-row>
          <v-col>
            <v-card>
              <v-card-title></v-card-title>
              <div class="buttons">
                <v-btn
                  class="round Item"
                  v-for="btn in transStore"
                  :key="btn.id"
                  draggable="true"
                  @dragstart="startDrag($event, btn)"
                >
                  {{ btn.label }}
                </v-btn>
              </div>
              <v-divider></v-divider>
              <div class="buttons">
                <v-btn
                  class="round Item"
                  v-for="btn in distribStore"
                  :key="btn.id"
                  draggable="true"
                  @dragstart="startDrag($event, btn)"
                >
                  {{ btn.label }}
                </v-btn>
              </div>
            </v-card>
          </v-col>
        </v-row>

        <v-row>
          <v-col>
            <v-card>
              <v-card-title>NWAs</v-card-title>
              <div class="buttons nwas">
                <v-btn
                  class="round Item"
                  v-for="btn in nwasStore"
                  :key="btn.id"
                  draggable="true"
                  @dragstart="startDrag($event, btn)"
                >
                  {{ btn.label }}
                </v-btn>
              </div>
            </v-card>
          </v-col>
        </v-row>

        <v-row>
          <v-col>
            <v-card class="cntTopology">
              <v-card-title>Topology</v-card-title>
              <v-card-text class="mt-3">
                <p class="text-body-1">
                  <strong> :</strong> {{ maxTopology }} 
                </p>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </div>

    <div class="arrangeArea">
      <div class="drop-zones">
        <div
          v-for="zone in dropZones"
          :key="zone.id"
          class="drop-zone"
          :class="{ 'invalid-zone': !zone.isValid }"
          @dragover="onDragOver($event, zone.id)"
          @dragleave="resetZoneColors"
          @drop="onDrop($event, zone.id)"
        >
          <h3>{{ zone.name }}</h3>
          <ul class="list-unstyled m-0 p-0">
            <li v-for="item in zone.items" :key="item.id">
              <div
                class="round Item"
                @contextmenu.prevent="removeItem(zone.id, item.uniqueId)"
              >
                <div
                  class="header"
                  :class="{ 'blue-header': item.setProperty }"
                >
                  {{ item.label }}
                </div>

                <div class="content">
                  <button
                    class="childButton"
                    :class="{ 'gray-button': item.id === 1 }"
                    @click="openDialog(item.id, item.uniqueId, item.label)"
                  >
                    
                  </button>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>

      <div class="step-controls">
        <v-btn class="step-button" @click="goToNextStep">  </v-btn>
      </div>
    </div>
  </div>

  <v-dialog v-model="showDialog" max-width="400px">
    <v-card>
      <v-card-title>  </v-card-title>
      <v-card-text> {{ dialogText }} </v-card-text>
      <v-card-actions>
        <v-btn color="primary" @click="showDialog = false"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>


<script setup>
import { ref, watch, defineEmits } from "vue";
import { useDropZoneStore } from "../store/dropZoneStore.js";
import { useItemStore } from "../store/itemStore.js";
import { useProgressStore } from "../store/progressStore.js";
import { useTopologyStore } from "../store/topologyStore.js";
import { storeToRefs } from "pinia";
import { useRouter } from "vue-router";

const store = useDropZoneStore();
const topologyStore = useTopologyStore();

const { dropZones } = storeToRefs(store);

const itemStore = useItemStore();
const { transStore } = storeToRefs(itemStore);
const { distribStore } = storeToRefs(itemStore);
const { nwasStore } = storeToRefs(itemStore);

const startItem = ref(null);
const maxTopology = ref(0);

const progressStore = useProgressStore();
const router = useRouter();
const demandArea = ref("");

var dialogText = ref("");
const showDialog = ref(false);

const uploadMode = ref("manual");
const nativeFileInput = ref(null);
const nativeDropZone = ref(null);
const dragging = ref(false);
const fileName = ref("");
const fileSize = ref("");

//  
function triggerNativeFile() {
  if (nativeFileInput.value) {
    nativeFileInput.value.value = "";
    nativeFileInput.value.click();
  }
}

//   
function onNativeFileChange(e) {
  const file = e.target.files?.[0];
  if (file) handleNativeFile(file);
}

//   
function onNativeDrop(e) {
  dragging.value = false;
  const file = e.dataTransfer?.files?.[0];
  if (file) handleNativeFile(file);
}

//  
async function handleNativeFile(file) {
  fileName.value = file.name;
  fileSize.value = formatSize(file.size);

  const dropZoneStore = useDropZoneStore();   //   !

  try {
    const content = await readFileContent(file);

    // 1) BOM 
    const clean = content.replace(/^\uFEFF/, "");

    // 2) JSON parse
    let parsed;
    try {
      parsed = JSON.parse(clean);
    } catch (e) {
      console.error("JSON  :", e);
      return;
    }

    // 3)   
    if (!Array.isArray(parsed)) {
      console.error(" JSON  :", parsed);
      return;
    }

    // 4) topologyStore 
    topologyStore.topologyList = parsed;

    // 5) DropZoneStore 
    const dz = convertTopologyToDropZone(parsed);

    //  Pinia Store 
    dropZoneStore.dropZones = dz;

    console.log("DropZoneStore  :", dz);

    
    progressStore.setStepCondition("step1", true);
    progressStore.setStepCondition("step2", true);
    progressStore.setStepCondition("step3", true);
    router.push("/step4"); // Step2 

  } catch (err) {
    console.error("   :", err);
  }
}

// X 
function clearFile() {
  fileName.value = "";
  fileSize.value = "";
  if (nativeFileInput.value) nativeFileInput.value.value = "";
}

//   
function formatSize(size) {
  const kb = size / 1024;
  return kb < 1024 ? `${kb.toFixed(1)} KB` : `${(kb / 1024).toFixed(1)} MB`;
}

function readFileContent(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();

    // CSV, JSON, TXT   
    if (file.name.endsWith(".csv") ||
        file.name.endsWith(".txt") ||
        file.name.endsWith(".json")) {
      reader.readAsText(file, "utf-8");
    }
    // XLSX  ArrayBuffer 
    else if (file.name.endsWith(".xlsx")) {
      reader.readAsArrayBuffer(file);
    }
    else {
      reject("   .");
      return;
    }

    reader.onload = () => resolve(reader.result);
    reader.onerror = () => reject(reader.error);
  });
}

function convertTopologyToDropZone(topologyList) {
  const dropZones = [
    { id: 1, name: "", items: [], isValid: true },
    { id: 2, name: "765kV", items: [], isValid: true },
    { id: 3, name: "345kV", items: [], isValid: true },
    { id: 4, name: "154kV", items: [], isValid: true },
    { id: 5, name: "22.9kV", items: [], isValid: true },
    { id: 6, name: "", items: [], isValid: true },
    { id: 7, name: "NWAs", items: [], isValid: true },
  ];

  // topologyList = [ [items...], [items...] ... ]
  topologyList.forEach(group => {
    if (!Array.isArray(group)) return;

    group.forEach(item => {
      if (!item || item.id == null) return;

      const zone = dropZones.find(z => z.id === Number(item.id));
      if (zone) {
        zone.items.push({ ...item });
      }
    });
  });

  //   zone  label   
  dropZones.forEach(zone => {
    zone.items = zone.items.filter(
      (item, index, self) =>
        index === self.findIndex(i => i.label === item.label)
    );
  });

  return dropZones;
}
const emit = defineEmits(["openDialog"]);

const openDialog = (id, uniqueId, label) => {
  emit("openDialog", id, uniqueId, label);
};

// 
const openPostcode = () => {
  new window.daum.Postcode({
    oncomplete: function (data) {
      demandArea.value = data.address;
    },
  }).open();
};

//    
const startDrag = (event, item) => {
  event.dataTransfer.effectAllowed = "move";
  event.dataTransfer.setData("draggedItem", JSON.stringify(item));
  startItem.value = item;
};

//  
const onDrop = (event, zoneId) => {
  const draggedItem = JSON.parse(event.dataTransfer.getData("draggedItem"));

  store.addItemToZone(zoneId, draggedItem);
  resetZoneColors();
  startItem.value = null;
};

//   
const onDragOver = (event, zoneId) => {
  event.preventDefault();

  if (!startItem.value) return;

  const isValid = zoneId === startItem.value.id;

  event.dataTransfer.dropEffect = isValid ? "move" : "none";

  dropZones.value.forEach((zone) => {
    zone.isValid = zone.id === startItem.value.id;
  });
};

const resetZoneColors = () => {
  dropZones.value.forEach((zone) => (zone.isValid = true));
};

// function validateHoursPerDay(value){
//   if (value > 24){
//     dialogText = "    24 .";
//     showDialog.value = true;
//     hoursPerDay.value = ref('')
//   }
// }

const addDemandArea = () => {
  const result = store.saveDemandArea(demandArea);
  if (result === "exists") {
    dialogText = "  .";
    showDialog.value = true;
  }
};

//  dropzone     
watch(
  dropZones,
  (newZones) => {
    //  765(Zone ID: 2)   Zone   
    const relevantZones = newZones.filter((zone) => zone.id !== 2);

    const allFilled = relevantZones.every((zone) => zone.items.length > 0);

    if (allFilled) {
      //  maxTopology = (765 )  zone items  
      maxTopology.value = newZones.reduce(
        (product, zone) => product * (zone.items.length || 1), //   zone 1 
        1
      );
    } else {
      maxTopology.value = 0;
    }
  },
  { deep: true, immediate: true }
);

const removeItem = (zoneId, uniqueId) => {
  store.removeItemFromZone(zoneId, uniqueId);
  topologyStore.clearTopologyList();
};

//   
const isStep1Completed = () => {
  // 1. DropZoneStore    (ID 6)
  const demandZone = dropZones.value.find((zone) => zone.id === 6);
  const hasDemandData = demandZone?.items?.length > 0; //    

  if (!hasDemandData) {
    dialogText = "   .";
    showDialog.value = true;
    return false;
  }

  // 2. 765   dropZone   
  const relevantZones = dropZones.value.filter((zone) => zone.id !== 2);
  const allFilled = relevantZones.every((zone) => zone.items.length > 0);

  if (!allFilled) {
    dialogText = " DropZone   . (765 )";
    showDialog.value = true;
    return false;
  }

  return true; //    true 
};

// ""      + 
const goToNextStep = () => {
  if (isStep1Completed()) {
    //   
    progressStore.setStepCondition("step1", true); // Step1   
    router.push("/step2"); // Step2 
    console.log("step1 DropZones : ", JSON.stringify(dropZones.value));
  } else {
    progressStore.setStepCondition("step1", false); // Step1   
  }
};
</script>


<style lang="scss" scoped>
.step_container {
  display: flex;
}

#daumLocation {
  margin-left: 16px;
  background: rgb(var(--dark-alt));
  border: none;
  border-radius: 2px;
  padding: 4px 8px;
  color: white;
  font-size: 14px;
  font-weight: bold;
}

.v-btn-toggle .v-btn {
  width: 48%;
}

.gray-panel.disabled {
  opacity: 0.5;
  pointer-events: none;
  transition: 0.3s;
}

.gray-panel {
  transition: 0.3s;
}

.upload-wrapper {
  margin-top: 10px;
}

.native-drop-zone {
  border: 2px dashed #90caf9;
  border-radius: 12px;
  padding: 50px;
  text-align: center;
  cursor: pointer;
  transition: 0.3s;
  background: #fafafa;
}

.native-drop-zone.active {
  background: #e3f2fd;
  border-color: #1976d2;
}

/*     */
.file-display {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  font-size: 1.2rem;
  color: #333;
}

/* X   */
.v-btn--size-x-small {
  height: 20px !important;
  width: 20px !important;
  min-width: 20px !important;
  padding: 0 !important;
}

.gray-panel.disabled {
  opacity: 0.4;
  pointer-events: none;
}

.btn-toggle-full {
  display: flex;
  width: 100%;
}

.btn-toggle-full .v-btn {
  flex: 1; /*        */
  justify-content: center;
  font-weight: 500;
}

.custom-toggle .v-btn.v-btn--active {
  background-color: #063480 !important;
  color: white !important;
}
</style>