<template>
  <!-- ========================= -->
  <!-- Dialog Root -->
  <!-- ========================= -->
  <v-dialog
    v-model="dialog"
    transition="dialog-transition"
    persistent
    scrollable
    max-width="550px"
  >
    <!-- ========================= -->
    <!-- Card Wrapper -->
    <!-- ========================= -->
    <v-card class="dialog-card elevation-8">
      <!-- Header -->
      <v-card-title
        class="dialog-header d-flex align-center justify-space-between"
      >
        <div class="d-flex align-center">
          <span class="text-h6">{{ props.label }}  </span>
        </div>
        <v-btn icon variant="text" @click="closeDialog">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>

      <!-- ========================= -->
      <!-- Body -->
      <!-- ========================= -->
      <v-card-text class="dialog-body">
        <!-- 22.9kV   -->
        <template v-if="is229 || Number(props.id) === 7">
          <div class="mb-4 d-flex align-center gap-3">
            <!--  /  /  -->
            <v-btn-toggle v-model="expansionType" mandatory>
              <v-btn value=""></v-btn>
              <v-btn v-if="is229" value=""></v-btn>
              <v-btn v-else value=""></v-btn>
            </v-btn-toggle>

            <!--   -->
            <v-btn-toggle v-if="is229" v-model="powerType" mandatory>
              <v-btn value="AC">AC</v-btn>
              <v-btn value="DC">DC</v-btn>
            </v-btn-toggle>
          </div>
        </template>

        <!-- ========================= -->
        <!--   -->
        <!-- ========================= -->
        <v-simple-table
          class="property-table rounded-lg overflow-hidden w-100"
          dense
        >
          <template #default>
            <table class="custom-table">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <template v-for="(section, i) in topologyData" :key="i">
                  <!--  depth1  -->
                  <tr
                    v-if="section.name && section.name.trim() !== ''"
                    class="section-title depth1-title"
                  >
                    <td colspan="3">{{ section.name }}</td>
                  </tr>

                  <!--  depth2 / depth3  -->
                  <template
                    v-for="(item, j) in getFilteredValues(section)"
                    :key="`${i}-${j}`"
                  >
                    <!-- depth2   -->
                    <tr
                      v-if="item.depth === 2 && item.data === null"
                      class="depth2-title"
                    >
                      <td colspan="3">
                        <v-icon size="14" color="primary" class="mr-1"
                          >mdi-chevron-right</v-icon
                        >
                        {{ item.name }}
                      </td>
                    </tr>

                    <!-- depth3   -->
                    <tr
                      v-if="item.depth === 3"
                      class="value-row hoverable depth3-row"
                    >
                      <td :class="item.is_default ? 'blue-text' : 'red-text'">
                        <span class="depth3-indent"></span>
                        {{ formatSubName(item.name) }}
                      </td>

                      <td class="td-value">
                        <v-text-field
                          v-if="item.modify"
                          v-model.number="item.data"
                          type="number"
                          variant="outlined"
                          density="compact"
                          hide-details
                          class="input-field"
                          :class="item.is_default ? 'blue-text' : 'red-text'"
                        />
                        <span
                          v-else
                          :class="item.is_default ? 'blue-text' : 'red-text'"
                        >
                          {{ item.data }}
                        </span>
                      </td>

                      <td class="td-unit">
                        <v-chip
                          size="small"
                          color="grey-lighten-3"
                          style="color: #1e1e1e; font-weight: 600"
                        >
                          {{ item.unit }}
                        </v-chip>
                      </td>
                    </tr>

                    <!--  depth2  -->
                    <tr
                      v-if="item.depth === 2 && item.data !== null"
                      class="value-row hoverable"
                    >
                      <td :class="item.is_default ? 'blue-text' : 'red-text'">
                        {{ item.name }}
                      </td>

                      <td class="td-value">
                        <v-text-field
                          v-if="item.modify"
                          v-model.number="item.data"
                          type="number"
                          variant="outlined"
                          density="compact"
                          hide-details
                          class="input-field"
                          :class="item.is_default ? 'blue-text' : 'red-text'"
                        />
                        <span
                          v-else
                          :class="item.is_default ? 'blue-text' : 'red-text'"
                        >
                          {{ item.data }}
                        </span>
                      </td>

                      <td class="td-unit">
                        <v-chip
                          size="small"
                          color="grey-lighten-3"
                          style="color: #1e1e1e; font-weight: 600"
                        >
                          {{ item.unit }}
                        </v-chip>
                      </td>
                    </tr>
                  </template>
                </template>
              </tbody>
            </table>
          </template>
        </v-simple-table>
      </v-card-text>

      <!-- ========================= -->
      <!-- Footer -->
      <!-- ========================= -->
      <v-card-actions class="dialog-footer justify-end pa-4">
        <v-btn color="primary" variant="elevated" rounded="lg" @click="save">
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>


<script setup>
import { ref, watch, nextTick, defineProps, defineEmits, computed } from "vue";
import { useDropZoneStore } from "../store/dropZoneStore";
import { useTopologyStore } from "../store/topologyStore";

const props = defineProps({
  modelValue: Boolean,
  label: String,
  uniqueId: String,
  id: Number,
});

const emit = defineEmits(["update:modelValue", "saveData"]);

const dropZoneStore = useDropZoneStore();
const topologyStore = useTopologyStore();

const dialog = ref(false);
const topologyData = ref([]);

// group control
const expansionType = ref("");
const powerType = ref("AC");

// =====================
// computed for label check
// =====================
//

const is229 = computed(() => props.label?.includes("22.9"));

// const is154 = computed(() => props.label?.includes("154"));

// =====================
// dialog sync
// =====================

const closeDialog = () => {
  emit("update:modelValue", false);
};

const loadProperty = async () => {
  await nextTick();
  const data = dropZoneStore.getProperty(props.uniqueId);
  if (data && data.property) {
    topologyData.value = JSON.parse(JSON.stringify(data.property));
  } else {
    topologyData.value = [];
  }
};

watch(
  () => props.modelValue,
  (val) => {
    dialog.value = val;
    if (val) loadProperty();
  },
  { immediate: true }
);

// =====================
//  
// =====================
const getFilteredValues = (item) => {
  if (!item?.values) return [];

  // NWAs  (id === 7)
  if (Number(props.id) === 7) {
    return item.values.filter((v) => {
      // group , /   
      return !v.group || v.group === expansionType.value;
    });
  }

  // 22.9kV  (/ + AC/DC)
  if (is229.value) {
    return item.values.filter((v) => {
      if (!v.group) return true;
      const [main, sub] = v.group.split("-");
      return (
        (!main || main === expansionType.value) &&
        (!sub || sub === powerType.value)
      );
    });
  }

  //  (154 )
  return item.values.filter((v) => !v.group);
};

// =====================
// 
// =====================
const save = () => {
  if (!props.uniqueId) return;

  //  1.   
  const cleaned = JSON.parse(JSON.stringify(topologyData.value));

  //  2. dropZoneStore   
  const zoneIdx = dropZoneStore.dropZones.findIndex((z) =>
    z.items.some((it) => it.uniqueId === props.uniqueId)
  );
  if (zoneIdx < 0) return;

  const itemIdx = dropZoneStore.dropZones[zoneIdx].items.findIndex(
    (it) => it.uniqueId === props.uniqueId
  );
  if (itemIdx < 0) return;

  const targetItem = dropZoneStore.dropZones[zoneIdx].items[itemIdx];

  //  3.   saveDemandArea 
  if (props.label.includes("")) {
    console.log("     saveDemandArea ");

    let address = "";
    let capacity = null;
    let newHoursPerDay = null;

    cleaned.forEach((section) => {
      section.values?.forEach((v) => {
        if (v.name === "") address = v.data;
        if (v.name.replace(/\s/g, "") === "") capacity = v.data;
        if (v.name.replace(/\s/g, "") === "")
          newHoursPerDay = v.data;
      });
    });

    dropZoneStore.saveDemandArea(address, capacity, newHoursPerDay, props.uniqueId);
  }

  //  4. dropZoneStore  property / setProperty 
  targetItem.property = JSON.parse(JSON.stringify(cleaned));
  targetItem.setProperty = true;
  targetItem.modifiedAt = new Date().toISOString();


  //  5. expansionType / powerType  
  if (is229.value || Number(props.id) === 7) {
    targetItem.expansionType = expansionType.value; // /
  }
  if (is229.value) {
    targetItem.powerType = powerType.value; // AC/DC
  }

  //  5. topologyStore 
  topologyStore.topologyList.forEach((zone) =>
    zone.forEach((item) => {
      if (item.uniqueId === props.uniqueId) {
        item.property = JSON.parse(JSON.stringify(cleaned));
        item.setProperty = true;
        item.modifiedAt = new Date().toISOString();
        
        //   
        if (is229.value || Number(props.id) === 7) {
          item.expansionType = expansionType.value;
        }
        if (is229.value) {
          item.powerType = powerType.value;
        }
      }
    })
  );

  //  6. emit  dialog 
  emit("saveData", JSON.parse(JSON.stringify(dropZoneStore.dropZones)));
  emit("update:modelValue", false);
  dialog.value = false;

  console.log(`  : ${props.label} (${props.uniqueId})`);
};


const formatSubName = (name) => {
  return name
    .replace(/^.*_/, "") //  `_`  
    .replace(/_/g, " "); //  `_` 
};
</script>

<style scoped>
/*  */
/* Dialog Layout */
/*  */
.dialog-card {
  border-radius: 24px;
  overflow: hidden;
  width: 100%;
}

.v-dialog > .v-card {
  width: 100% !important;
  max-width: 100% !important;
  margin: 0 !important;
}

.dialog-header {
  background: linear-gradient(90deg, #f8f9fb, #ffffff);
  border-bottom: 1px solid #e0e0e0;
  padding: 10px 18px;
  font-weight: 600;
}

/*  */
/* Table Layout */
/*  */
.property-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 4px;
}

.custom-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 4px;
  table-layout: fixed;
}

/*  (1)    */
.property-table th {
  background-color: #f5f6fa;
  padding: 12px;
  text-align: center;
  font-weight: 700; /*   */
  font-size: 1rem; /*   */
  border-bottom: 2px solid #d0d7e2;
  color: #1f2937;
}

/*  (3)   */
.custom-table th:nth-child(1),
.custom-table td:nth-child(1) {
  width: 30%;
  text-align: center; /*   */
}
.custom-table th:nth-child(2),
.custom-table td:nth-child(2) {
  width: 40%;
  text-align: center; /*   */
}
.custom-table th:nth-child(3),
.custom-table td:nth-child(3) {
  width: 30%;
  text-align: left; /*    */
}

/*  (2) Depth     ,  */
.section-title.depth1-title td,
.depth2-title td {
  background: #e7efff;
  font-weight: 700;
  font-size: 1rem;
  color: #063480;
  padding: 8px 12px;
  border-top: 2px solid #d0dbf4;
  border-bottom: 1px solid #dce3ec;
}

.depth2-title td {
  background: #f3f6fb;
  border-top: 1px solid #d6e0f0;
}

/* Depth3  (  ) */
.depth3-row td {
  background: #ffffff;
  padding-left: 24px;
}

.depth3-indent {
  color: #888;
  margin-right: 4px;
  font-size: 0.8rem;
}

/*    */
.value-row td {
  padding: 8px 10px;
  vertical-align: middle;
}

.value-row.hoverable:hover {
  background-color: #f8f9fa;
  transition: 0.2s;
}

/*    */
.input-field {
  max-width: 140px;
  font-size: 0.9rem;
}

.blue-text {
  color: #1565c0;
  font-weight: 500;
}

.red-text {
  color: #c01515;
  font-weight: 500;
}

/*   */
::v-deep(.v-chip__content) {
  color: #1e1e1e !important;
  font-weight: 600 !important;
  text-align: left !important;
}

/* Dialog transition */
::v-deep(.v-overlay:nth-of-type(2)) {
  z-index: 202 !important;
  transition: opacity 0.12s ease !important;
}
::v-deep(.dialog-transition-enter-active),
::v-deep(.dialog-transition-leave-active) {
  transition-duration: 0.18s !important;
}
</style>
