  //    ( 6 )
  const addTopology = (newTopology) => {
    if (topologyList.value.length >= MAX_TOPOLOGY_COUNT) {
      return false; // 6    
    }
    if (!topologyList.value.includes(newTopology)) {
      topologyList.value.push(newTopology);
    }

    return true; //  
  };

  const isNotAdd = () => {
    return topologyList.value.length >= MAX_TOPOLOGY_COUNT;
  }

  const removeTopology = (index) => {
    topologyList.value.splice(index, 1);
  };

  const setTopologyList = (newList) => {
    topologyList.value = newList;
  };

  const clearTopologyList = () => {
    topologyList.value = [];
  }

  const setCoordinates = (uniqueId, lat, lng) => {
    if (!Array.isArray(topologyList.value)) {
      console.error("topologyList.value  .", topologyList.value);
      return;
    }
  
    //    (      forEach )
    topologyList.value.forEach((group) => {
      if (!Array.isArray(group)) return;
  
      group.forEach((item) => {
        if (item.uniqueId === uniqueId) {
          console.log(` : ${uniqueId}  lat: ${lat}, lng: ${lng}`);
  
          // Vue   (Object.assign )
          Object.assign(item, { lat, lng });
        }
      });
    });
  };



  return { topologyList, addTopology, isNotAdd, removeTopology, setTopologyList, clearTopologyList, setCoordinates};
