<template>
  <div class="app">
    <SideBar />
    <router-view v-slot="{ Component }" @openDialog="openDialog">
      <component :is="Component" v-if="Component" />
    </router-view>
        <GlobalDialog
          v-if="dialog"
          v-model="dialog"
          :id="dialogId"
          :uniqueId="dialogUniqueId"
          :label="dialogLabel"
          @saveData="handleSave"
        />
  </div>
</template>

<script setup>
//  or  
import SideBar from "./components/SideBar.vue";
import { ref, watch } from "vue";

import GlobalDialog from "@/components/GlobalDialog.vue"; //   


const dialog = ref(false);
const dialogId = ref("");
const dialogUniqueId = ref("");
const dialogLabel = ref("");


const openDialog = (id, uniqueId, label) => {
  dialogId.value = id;
  dialogUniqueId.value = uniqueId;
  dialogLabel.value = label;
  dialog.value = true;
};

const closeDialog = () => {
  console.log("     ");
  dialog.value = false;
  dialogId.value = "";
  dialogUniqueId.value = ""; //  uniqueId 
  dialogLabel.value = "";
};


watch(dialog, (val) => {
  if (!val) {
    closeDialog()  //   false()  
  }
})

const handleSave = (data) => {
  console.log('  ', data);
};

console.log('  : ', '2025-04-21 04-21');

</script>


