<template>
  <div class="result-page">
    <!--   -->
    <div class="header">
      <h2>Simulation </h2>
    </div>

    <!--    () -->
    <div class="menu-bar">
      <div
        class="menu-title"
        :class="{ active: selectedTab === 'cost' }"
        @click="selectedTab = 'cost'"
      >
           
      </div>

      <div
        class="menu-title"
        :class="{ active: selectedTab === 'carbon' }"
        @click="selectedTab = 'carbon'"
      >
        
      </div>
    </div>

    <!--    -->
    <v-container class="content-container" fluid>
      <!-- [1]     -->
      <v-row v-show="selectedTab === 'cost'">
        <!-- :  -->
        <v-col cols="8">
          <v-card class="graph-card" outlined>
            <v-row class="align-center mb-2">
              <v-col cols="auto">
                <v-select
                  v-model="selectedGroupIndex"
                  :items="groupOptions"
                  item-title="title"
                  item-value="value"
                  label="Topology List"
                  density="compact"
                  variant="outlined"
                  hide-details
                  style="width: 200px"
                />
              </v-col>
              <!--   Topology   -->
              <v-col class="text-grey text-h6">
                <p
                  v-for="(node, nodeIndex) in getSelectedList"
                  :key="node.uniqueId || nodeIndex"
                  class="d-inline selectedList"
                >
                  {{ node.label }}
                  <span v-if="nodeIndex < getSelectedList.length - 1">
                    {{
                      (() => {
                        const current = node.id;
                        const next = getSelectedList[nodeIndex + 1]?.id;
                        const hasDemandNode = getSelectedList.some(
                          (n) => n.id === 6
                        );
                        if (!hasDemandNode) return "  ";
                        const demandIndex = getSelectedList.findIndex(
                          (n) => n.id === 6
                        );
                        return nodeIndex < demandIndex ? "  " : "  ";
                      })()
                    }}
                  </span>
                </p>
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="6">
                <h4 class="chart-title">
                   - NWAs     
                </h4>
                <canvas ref="barChart" height="220"></canvas>
              </v-col>
              <v-col cols="6">
                <h4 class="chart-title"> - NWAs   </h4>
                <canvas ref="doughnutChart" height="220"></canvas>
              </v-col>
            </v-row>
          </v-card>
        </v-col>

        <!-- :   -->
        <v-col cols="4">
          <v-card class="table-card" outlined>
            <div class="table-header">
              <span></span>
              <span class="unit-label"> : </span>
            </div>
            <v-data-table
              :headers="tableHeaders"
              :items="tableItems"
              :items-per-page="-1"
              hide-default-footer
              density="compact"
              virtual-scroll="false"
              style="max-height: none; overflow: visible"
            >
              <template v-slot:item="{ item }">
                <tr>
                  <td class="text-left font-weight-bold">
                    {{ item.category }}
                  </td>
                  <td
                    v-for="(topology, idx) in topologyList"
                    :key="idx"
                    class="text-right"
                  >
                    {{ item[`list${idx + 1}`] }}
                  </td>
                </tr>
              </template>
            </v-data-table>
          </v-card>
        </v-col>
      </v-row>

      <!-- [2]  -->
      <v-row v-show="selectedTab === 'carbon'">
        <!-- :  -->
        <v-col cols="8">
          <v-card class="graph-card" outlined>
            <v-row class="align-center mb-2">
              <v-col cols="auto">
                <v-select
                  v-model="selectedGroupIndex"
                  :items="groupOptions"
                  item-title="title"
                  item-value="value"
                  label="Topology List"
                  density="compact"
                  variant="outlined"
                  hide-details
                  style="width: 200px"
                />
              </v-col>

              <!--   Topology   -->
              <v-col class="text-grey text-h6">
                <p
                  v-for="(node, nodeIndex) in getSelectedList"
                  :key="node.uniqueId || nodeIndex"
                  class="d-inline selectedList"
                >
                  {{ node.label }}
                  <span v-if="nodeIndex < getSelectedList.length - 1">  </span>
                </p>
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="6">
                <h4 class="chart-title"> - NWAs  </h4>
                <canvas ref="carbonBarChart" height="220"></canvas>
              </v-col>
              <v-col cols="6">
                <h4 class="chart-title"> - NWAs  </h4>
                <canvas ref="carbonDoughnutChart" height="220"></canvas>
              </v-col>
            </v-row>
          </v-card>
        </v-col>

        <!-- :   -->
        <v-col cols="4">
          <v-card class="table-card" outlined>
            <div class="table-header">
              <span></span>
              <span class="unit-label"> : tCO/MWh</span>
            </div>
            <v-data-table
              :headers="carbonTableHeaders"
              :items="carbonTableItems"
              class="cost-table"
              hide-default-footer
            />
          </v-card>
        </v-col>
      </v-row>
    </v-container>

    <!-- :   -->
    <div class="footer-bar">
      <v-btn color="primary" class="report-btn" @click="exportAllToPDF">
         
      </v-btn>
    </div>
  </div>
</template>


<script setup>
import { ref, onMounted, watch, nextTick, computed } from "vue";
import Chart from "chart.js/auto";
import ChartDataLabels from "chartjs-plugin-datalabels";
import html2pdf from "html2pdf.js";
import { useTopologyStore } from "../store/topologyStore.js";
import { storeToRefs } from "pinia";

//  
Chart.register(ChartDataLabels);

// ==============================
//  Store 
// ==============================
const topologyStore = useTopologyStore();
const { topologyList } = storeToRefs(topologyStore);

//   
const selectedTab = ref("cost");

//   ()
const carbonTableHeaders = [
  { title: "", key: "label", align: "start" },
  { title: "List 1", key: "value", align: "end" },
];
const carbonTableItems = ref([
  { label: "", value: "12.4" },
  { label: "NWAs", value: "7.6" },
  { label: "", value: "20.0" },
]);

// ==============================
//  Topology  
// ==============================
const arrayToMap = (topologyList) => {
  return topologyList.map((route) => {
    // route    
    if (!Array.isArray(route)) return new Map();

    const entries = route
      .filter((node) => node && typeof node === "object") //  null/undefined 
      .map((node) => {
        //  label   
        const label = node.label?.replace(/-[A-Z]$/i, "") || "";
        const props = Array.isArray(node.property) ? node.property : [];
        const formatted = {};

        //  property   
        props.forEach((prop) => {
          const name = prop?.name;
          const values = Array.isArray(prop?.values) ? prop.values : [];
          if (!name || !values.length) return;

          if (values.length === 1 && "data" in values[0]) {
            formatted[name] = values[0].data;
          } else if (values.every((v) => "name" in v && "data" in v)) {
            formatted[name] = values.reduce((obj, v) => {
              if (!v.name) return obj;
              obj[v.name] = v.data;
              return obj;
            }, {});
          }
        });

        //     
        formatted.property = node.property ?? [];
        formatted.lat = node.lat ?? 0;
        formatted.lng = node.lng ?? 0;
        formatted.id = node.id ?? null; //    (NWAs )

        return [label, formatted];
      });

    return new Map(entries);
  });
};

const map_topologyList = computed(() => arrayToMap(topologyList.value));

console.log("map_topologyList", topologyList.value);

// ==============================
//  Topology 
// ==============================
const selectedGroupIndex = ref(0);
const groupOptions = computed(() =>
  topologyList.value.map((_, idx) => ({
    title: `Topology List ${idx + 1}`,
    value: idx,
  }))
);

const getSelectedList = computed(() => {
  if (!topologyList.value || topologyList.value.length === 0) return [];

  const index = selectedGroupIndex.value;
  const selected = topologyList.value[index];

  // selected  list 
  console.log("selectedList", selected);
  return Array.isArray(selected) ? selected : [];
});
// ==============================
//  Chart refs
// ==============================
const barChart = ref(null);
const doughnutChart = ref(null);
let barChartInstance = null;
let doughnutChartInstance = null;

// ==============================
//  Table 
// ==============================
const tableHeaders = computed(() => {
  const headers = [{ title: "", key: "category", align: "start" }];
  topologyList.value.forEach((_, idx) => {
    headers.push({
      title: `List ${idx + 1}`,
      key: `list${idx + 1}`,
      align: "end",
    });
  });
  return headers;
});

const tableItems = ref([]);

// ==============================
//     ( )
// ==============================
const getDistance = (lat1, lng1, lat2, lng2) => {
  const R = 6371e3; // m
  const rad = Math.PI / 180;
  const phi1 = lat1 * rad;
  const phi2 = lat2 * rad;
  const dPhi = (lat2 - lat1) * rad;
  const dLambda = (lng2 - lng1) * rad;

  const a =
    Math.sin(dPhi / 2) ** 2 +
    Math.cos(phi1) * Math.cos(phi2) * Math.sin(dLambda / 2) ** 2;
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  return (R * c) / 1000; // km
};

// ==============================
//    ( )
// ==============================

const calcCostData = (group, nwasNodes) => {
  const safeNum = (val) => {
    const num = Number(val);
    return isNaN(num) || !isFinite(num) ? 0 : num;
  };

  // ------------------------------
  //   
  // ------------------------------
  const demand =
    group.get("") || group.get("-A") || group.get("-B");
  const k765 = group.get("765kV") || group.get("765kV-A");
  const k345 = group.get("345kV") || group.get("345kV-A");
  const k154 = group.get("154kV") || group.get("154kV-A");
  const k22 = group.get("22.9kV") || group.get("22.9kV-A");

  // ------------------------------
  //   
  // ------------------------------
  let distance = 50;
  if (demand && k22 && demand.lat && k22.lat)
    distance = getDistance(demand.lat, demand.lng, k22.lat, k22.lng);

  // ------------------------------
  //     
  // ------------------------------
  const getTransmissionCost = (node, defaultLoss = 0.03) => {
    if (!node) return 0;

    const prop = node.property || [];
    const connect =
      prop.find((p) => p.name.includes("_1"))?.values ||
      [];
    const loss =
      prop.find((p) => p.name.includes("_1"))?.values ||
      [];

    const findVal = (arr, key) =>
      safeNum(arr.find((v) => v.name.replace(/\s/g, "").includes(key))?.data);

    const baseUnit = findVal(connect, "") || 667.61;
    const useUnit = findVal(connect, "") || 1.42;
    const lossUnit = findVal(loss, "") || defaultLoss;

    const demandProp = demand?.property?.[0]?.values || [];
    const demandMW = safeNum(
      demandProp.find((v) => v.name.includes(""))?.data
    );
    const demand_kW = demandMW * 1000;
    const dailyUsageHr = 8;

    //    +   
    const buildUnit = safeNum(
      node.property?.[0]?.values.find((v) => v.name.includes(""))?.data
    );
    const baseCost =
      buildUnit > 0
        ? buildUnit * 1_000_000 * 50
        : node.label?.includes("765")
        ? 5_700_000_000
        : node.label?.includes("345")
        ? 2_060_000_000
        : node.label?.includes("154")
        ? 102_000_000
        : 0;

    const construction = baseCost;
    const connection =
      demand_kW * useUnit * dailyUsageHr * 365 + demand_kW * baseUnit;
    const lossCost = demand_kW * (lossUnit / 1000) * dailyUsageHr * 365;

    return construction + connection + lossCost;
  };

  // ------------------------------
  //   
  // ------------------------------
  const cost765 = getTransmissionCost(k765);
  const cost345 = getTransmissionCost(k345);
  const cost154 = getTransmissionCost(k154);

  const prop22 =
    k22?.property?.find((p) => p.name.includes(""))?.values || [];

  const expansionType = k22?.expansionType || ""; //    
  const powerType = k22?.powerType || "AC"; //    
  const groupKey = `${expansionType}-${powerType}`;

  //      
  const filteredValues = prop22.filter((v) => v.group === groupKey);
  const distribSum = filteredValues.reduce(
    (sum, v) => sum + safeNum(v.data),
    0
  );
  const cost229 = distance * distribSum * 10_000;

  // ------------------------------
  //  NWAs 
  // ------------------------------
  const calcNWAsCost = (nodes) => {
    const total = {};

    nodes.forEach((node) => {
      const label = node.key?.replace(/-A|-B/g, "") || "NWAs";
      const props = Array.isArray(node.property) ? node.property : [];

      const selectedType = node.expansionType || ""; //   
      const target = props.find((p) => p?.name?.includes(""));
      const values = Array.isArray(target?.values) ? target.values : [];

      const newBuild = values.filter((v) => v.group === selectedType);

      //   
      const safeNum = (x) => (isNaN(Number(x)) ? 0 : Number(x));

      const getSum = (keywords) =>
        newBuild
          .filter((v) => keywords.some((k) => v.name?.includes(k)))
          .reduce((sum, v) => sum + safeNum(v.data), 0);

      //   (MW)
      const capacityMW = safeNum(
        props[0]?.values?.find((v) => v.name.includes(""))?.data
      );

      //    
      const convertToWon = (value, unit) => {
        switch (true) {
          case /\/kW/.test(unit):
            return safeNum(value) * 1000 * capacityMW * 1000; // MWkW 
          case /\/km/.test(unit):
            return safeNum(value) * 1_000_000; //      
          case /\//.test(unit):
            return safeNum(value) * 100_000_000;
          default:
            return safeNum(value);
        }
      };

      //   
      let cost = 0;

      switch (label) {
        // --------------------------
        //  /  / 
        // --------------------------
        case "":
        case "":
        case "": {
          newBuild.forEach((v) => {
            if (v.unit) cost += convertToWon(v.data, v.unit);
          });
          break;
        }

        // --------------------------
        // ESS ()
        // --------------------------
        case "ESS": {
          newBuild.forEach((v) => {
            if (v.unit) cost += convertToWon(v.data, v.unit);
          });
          break;
        }

        // --------------------------
        // EV ()
        // --------------------------
        case "EV": {
          const setup = getSum([""]);
          const maintain = getSum([""]);
          const permitRatio = newBuild.find((v) =>
            v.name.includes("")
          )?.data;
          const permitCost = (safeNum(setup) * safeNum(permitRatio)) / 100;
          cost = (setup + maintain + permitCost) * 1000 * capacityMW * 1000;
          break;
        }

        // --------------------------
        // EVC ()
        // --------------------------
        case "EVC": {
          newBuild.forEach((v) => {
            if (v.unit) cost += convertToWon(v.data, v.unit);
          });
          break;
        }

        // --------------------------
        // DR ()
        // --------------------------
        case "DR": {
          const unitSum = getSum([
            "",
            "",
            "",
            "",
            "",
          ]);
          cost = unitSum * 1000 * capacityMW * 1000;
          break;
        }

        // --------------------------
        // VPP ()
        // --------------------------
        case "VPP": {
          const unitSum = getSum([
            "",
            "",
            "",
            "",
            "",
          ]);
          cost = unitSum * 1000 * capacityMW * 1000;
          break;
        }

        default:
          cost = 0;
      }

      total[label] = Math.max(0, safeNum(cost));
    });

    return total;
  };
  const nwasCosts = calcNWAsCost(nwasNodes);
  const nwasSum = Object.values(nwasCosts)
    .map((v) => safeNum(v))
    .reduce((a, b) => a + b, 0);

  // ------------------------------
  //     
  // ------------------------------
  //   
  const transSum =
    safeNum(cost765) + safeNum(cost345) + safeNum(cost154) + safeNum(cost229);

  //     (NaN )
  const benefit = safeNum(transSum - nwasSum);
  const total = safeNum(transSum + nwasSum + benefit);

  // ------------------------------
  //  Chart.js  
  // ------------------------------
  const bar = {
    labels: ["", "NWAs", ""],
    datasets: [
      // [  ] 
      { label: "765kV", data: [cost765, 0, 0], backgroundColor: "#6366F1" },
      { label: "345kV", data: [cost345, 0, 0], backgroundColor: "#60A5FA" },
      { label: "154kV", data: [cost154, 0, 0], backgroundColor: "#FBBF24" },
      { label: "22.9kV", data: [cost229, 0, 0], backgroundColor: "#34D399" },
      ...Object.entries(nwasCosts).map(([key, val], idx) => ({
        label: key,
        data: [0, val, 0],
        backgroundColor: [
          "#E879F9",
          "#10B981",
          "#F87171",
          "#3B82F6",
          "#F59E0B",
          "#6366F1",
          "#22C55E",
          "#0EA5E9",
        ][idx],
      })),
      { label: "B/C", data: [0, 0, benefit], backgroundColor: "#FDBA74" },
    ],
  };

  const doughnut = {
    labels: ["765kV", "345kV", "154kV", "22.9kV"],
    datasets: [
      {
        data: [cost765, cost345, cost154, cost229],
        backgroundColor: ["#6366F1", "#60A5FA", "#FBBF24", "#34D399"],
      },
    ],
  };

  // ------------------------------
  //     (category  )
  // ------------------------------
  const nwasLabels = [
    "",
    "",
    "",
    "ESS",
    "EV",
    "EVC",
    "DR",
    "VPP",
  ];

  const fmt = (num) => {
    const safe = safeNum(num);
    if (safe === 0) return "-"; //  0  
    return Math.round(safe).toLocaleString("ko-KR", {
      maximumFractionDigits: 0,
    });
  };

  const table = [
    { category: "765kV", value: fmt(cost765) },
    { category: "345kV", value: fmt(cost345) },
    { category: "154kV", value: fmt(cost154) },
    { category: "22.9kV", value: fmt(cost229) },
    { category: " ", value: fmt(transSum) },
    ...nwasLabels.map((k) => ({
      category: k,
      value: safeNum(nwasCosts[k]) > 0 ? fmt(nwasCosts[k]) : "-",
    })),
    { category: "NWAs ", value: fmt(nwasSum) },
    { category: "", value: fmt(benefit) },
  ];

  return { bar, doughnut, table, total };
};

// ==============================
//  Chart 
// ==============================
const destroyCharts = () => {
  if (barChartInstance) barChartInstance.destroy();
  if (doughnutChartInstance) doughnutChartInstance.destroy();
  barChartInstance = doughnutChartInstance = null;
};
const createCharts = async () => {
  await nextTick();
  destroyCharts();

  const safeNum = (val) => {
    const num = Number(val);
    return isNaN(num) || !isFinite(num) ? 0 : num;
  };

  const topologyCount = topologyList.value.length;
  if (topologyCount === 0) return;

  const selectedIdx = selectedGroupIndex.value ?? 0;

  const categories = [
    "765kV",
    "345kV",
    "154kV",
    "22.9kV",
    " ",
    "",
    "",
    "",
    "ESS",
    "EV",
    "EVC",
    "DR",
    "VPP",
    "NWAs ",
    "",
  ];

  //     
  const table = categories.map((cat) => {
    const row = { category: cat };
    for (let i = 1; i <= topologyCount; i++) row[`list${i}`] = "-";
    return row;
  });

  //   Topology   ( )
  for (let i = 0; i < topologyCount; i++) {
    const group = map_topologyList.value[i];
    if (!group) continue;

    const nwasNodes = Array.from(group.entries())
      .filter(([_key, val]) => val?.id === 7)
      .map(([key, val]) => ({ key, ...val }));

    const {
      _bar,
      _doughnut,
      table: singleTable,
      _total,
    } = calcCostData(group, nwasNodes);

    singleTable.forEach(({ category, value }) => {
      const target = table.find((t) => t.category === category);
      if (target) target[`list${i + 1}`] = value;
    });

    //  /NWAs  
    const getSum = (keys) =>
      keys
        .map((k) => {
          const raw =
            table.find((t) => t.category === k)?.[`list${i + 1}`] || "0";
          const cleaned = typeof raw === "string" ? raw.replace(/,/g, "") : raw;
          return safeNum(cleaned);
        })
        .reduce((a, b) => a + b, 0);

    const sumTransmission = getSum(["765kV", "345kV", "154kV", "22.9kV"]);
    const sumNWAs = getSum([
      "",
      "",
      "",
      "ESS",
      "EV",
      "EVC",
      "DR",
      "VPP",
    ]);

    table.find((t) => t.category === " ")[`list${i + 1}`] =
      sumTransmission.toLocaleString();
    table.find((t) => t.category === "NWAs ")[`list${i + 1}`] =
      sumNWAs.toLocaleString();
    table.find((t) => t.category === "")[`list${i + 1}`] = (
      sumTransmission - sumNWAs
    ).toLocaleString();
  }

  //   Topology  
  const group = map_topologyList.value[selectedIdx];
  if (!group) {
    tableItems.value = table;
    return;
  }

  const nwasNodes = Array.from(group.entries())
    .filter(([_key, val]) => val?.id === 7)
    .map(([key, val]) => ({ key, ...val }));

  const { bar, _doughnut, _total } = calcCostData(group, nwasNodes);

  //   Topology 
  const getSelectedSum = (keys) =>
    keys
      .map((k) => {
        const raw =
          table.find((t) => t.category === k)?.[`list${selectedIdx + 1}`] ||
          "0";
        const cleaned = typeof raw === "string" ? raw.replace(/,/g, "") : raw;
        return safeNum(cleaned);
      })
      .reduce((a, b) => a + b, 0);

  const sumTransmission = getSelectedSum(["765kV", "345kV", "154kV", "22.9kV"]);
  const sumNWAs = getSelectedSum([
    "",
    "",
    "",
    "ESS",
    "EV",
    "EVC",
    "DR",
    "VPP",
  ]);

  //   
  if (barChart.value) {
    const ctx = barChart.value.getContext("2d");
    if (barChartInstance) barChartInstance.destroy();
    barChartInstance = new Chart(ctx, {
      type: "bar",
      data: bar,
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          x: { stacked: true, grid: { display: false } },
          y: {
            stacked: true,
            beginAtZero: true,
            ticks: {
              callback: (v) => (v / 1_000_000).toLocaleString() + "",
            },
          },
        },
        plugins: {
          legend: {
            position: "bottom",
            labels: { usePointStyle: true, boxWidth: 10, padding: 15 },
          },
          tooltip: {
            callbacks: {
              label: function (context) {
                const value = context.parsed.y;
                //       
                return `${Math.round(value).toLocaleString("ko-KR")} `;
              },
            },
          },
          datalabels: {
            anchor: "center",
            align: "center",
            color: "#000",
            font: { weight: "bold" },
            formatter: (v) =>
              v > 0
                ? `${Math.round(v / 1_000_000).toLocaleString("ko-KR")} `
                : "",
          },
        },
      },
    });
  }

  //    ( vs NWAs )
  if (doughnutChart.value) {
    const ctx = doughnutChart.value.getContext("2d");
    if (doughnutChartInstance) doughnutChartInstance.destroy();

    const transSum = sumTransmission;
    const nwasSum = sumNWAs;
    const totalSum = transSum + nwasSum;

    const doughnutData = {
      labels: ["", "NWAs"],
      datasets: [
        {
          data: [transSum, nwasSum],
          backgroundColor: ["#6A5DFF", "#FF6B6B"],
          borderWidth: 0,
        },
      ],
    };

    doughnutChartInstance = new Chart(ctx, {
      type: "doughnut",
      data: doughnutData,
      options: {
        responsive: true,
        maintainAspectRatio: false,
        cutout: "70%",
        plugins: {
          legend: {
            position: "bottom",
            labels: {
              usePointStyle: true,
              boxWidth: 10,
              padding: 20,
              generateLabels: (chart) => {
                const data = chart.data.datasets[0].data;
                const labels = chart.data.labels;
                const total = data.reduce((a, b) => a + b, 0);
                return labels.map((label, i) => {
                  const val = data[i];
                  const percent =
                    total > 0 ? ((val / total) * 100).toFixed(1) : 0;
                  return {
                    text: `${label} : ${Math.round(
                      val / 1_000_000
                    ).toLocaleString("ko-KR")} (${percent}%)`,
                    fillStyle: chart.data.datasets[0].backgroundColor[i],
                    hidden: false,
                    index: i,
                  };
                });
              },
            },
          },
          datalabels: {
            color: "#fff", //    ( )   
            align: "center",
            anchor: "center",
            font: {
              weight: "bold",
              size: 14,
            },
            formatter: (v) => {
              if (v <= 0) return "";
              return `${Math.round(v / 1_000_000).toLocaleString(
                "ko-KR"
              )} `;
            },
          },
          tooltip: {
            callbacks: {
              label: (ctx) => {
                const val = ctx.raw;
                const percent = ((val / totalSum) * 100).toFixed(1);
                return `${ctx.label}: ${Math.round(
                  val / 1_000_000
                ).toLocaleString("ko-KR")} (${percent}%)`;
              },
            },
          },
        },
      },
      plugins: [
        {
          id: "centerText",
          beforeDraw(chart) {
            const { width, height, ctx } = chart;
            const total = chart.data.datasets[0].data.reduce(
              (a, b) => a + b,
              0
            );
            ctx.save();
            ctx.font = "bold 28px Pretendard";
            ctx.fillStyle = "#000";
            ctx.textAlign = "center";
            ctx.textBaseline = "middle";
            ctx.fillText(
              Math.round(total / 1_000_000).toLocaleString("ko-KR"),
              width / 2,
              height / 2
            );
            ctx.font = "16px Pretendard";
            ctx.fillText("  ()", width / 2, height / 2 + 30);
            ctx.restore();
          },
        },
      ],
    });
  }

  //    
  tableItems.value = table;
  console.table(tableItems.value);
};

// ==============================
//  PDF 
// ==============================
const exportAllToPDF = async () => {
  const el = document.querySelector(".result-page");
  if (!el) return;
  await html2pdf()
    .set({
      margin: 0.5,
      filename: "Simulation_Result.pdf",
      image: { type: "jpeg", quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: "in", format: "a4", orientation: "portrait" },
    })
    .from(el)
    .save();
};

// ==============================
//  LifeCycle
// ==============================
onMounted(() => createCharts());
watch(selectedGroupIndex, () => createCharts());
</script>


